import React from "react";
import { connect } from "react-redux";
import { toDone, toInProgress, removeTodo } from "../../actions";

function TodoDetail({ todo, toDone, toInProgress, removeTodo, history }) {
  console.log(todo);
  return (
    <div>
      {todo ? (
        <>
          <h1>{todo.title}</h1>
          <div>
            <p>Descriptioin:</p>
            <span>{todo.description}</span>
            <p>Place:</p>
            <span>{todo.place}</span>
            <p>Date:</p>
            <span>{todo.date}</span>
            <p>Status:</p>
            <span>{todo.status}</span>
          </div>
          <br />
          <div className="btn-group" role="group">
            <button
              className="btn btn-outline-primary"
              onClick={() => {
                toDone(todo.id);
                alert("Status Cambiado");
                history("/");
              }}
            >
              Done
            </button>
            <button
              className="btn btn-outline-primary"
              onClick={() => {
                toInProgress(todo.id);
                alert("Status Cambiado");
                history("/");
              }}
            >
              In Progress
            </button>
            <button
              className="btn btn-outline-primary"
              onClick={() => {
                removeTodo(todo.id);
                alert("Tarea Eliminada");
                history("/");
              }}
            >
              Delete
            </button>
          </div>
        </>
      ) : (
        "No encontrado"
      )}
    </div>
  );
}

export default connect(null, { toDone, toInProgress, removeTodo })(TodoDetail);
