import React from "react";
import { connect } from "react-redux";
import { addTodo } from "./../../actions/index";

// Nota 1: Para utilizar el hook `useState` para el manejo de estados de los inputs, tendras que utilizarlo de la siguiente manera
//React.useState

// Nota 2: En este componente tendras que usar la funcion `connect` de react-redux para conectarte al store.
// Si usas el hook `useDispatch` no funcionaran los test.

export function AddTodo({ addTodo }) {
  const [form, setForm] = React.useState({
    title: "",
    description: "",
    place: "",
    date: "",
  });

  const handleChange = (e) => {
    setForm({
      ...form,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    addTodo(form);

    setForm({
      title: "",
      description: "",
      place: "",
      date: "",
    });
  };

  const style = {
    padding: " 2em 2em",
    width: "calc(50%- 4em)",
    textAlign: "center",
  };

  return (
    <form onSubmit={handleSubmit} style={style} className="p-lg-5">
      <div className="mb-3">
        <label className="form-label">Title</label>
        <input
          type="text"
          onChange={handleChange}
          value={form.title}
          name="title"
          className="form-control"
        ></input>
      </div>
      <div className="mb-3">
        <label className="form-label">Description</label>
        <textarea
          className="form-control"
          type="text"
          onChange={handleChange}
          value={form.description}
          name="description"
        ></textarea>
      </div>
      <div className="mb-3">
        <label className="form-label">Place</label>
        <input
          className="form-control"
          type="text"
          onChange={handleChange}
          value={form.place}
          name="place"
        ></input>
      </div>
      <div className="mb-3">
        <label className="form-label">Date</label>
        <input
          className="form-control"
          type="text"
          onChange={handleChange}
          value={form.date}
          name="date"
        ></input>
      </div>
      <button type="submit" className="btn btn-primary">
        Agregar
      </button>
    </form>
  );
}

export default connect(null, { addTodo })(AddTodo);
