import React from "react";
import Todos from "../Todos/Todos";

export function Home() {
  const style = {
    display: "flex",
    justifyContent: "space-around",
    margin: "2rem 0",
  };
  return (
    <div style={style}>
      <Todos status="Todo" />
      <Todos status="InProgress" />
      <Todos status="Done" />
    </div>
  );
}

export default Home;
